.container {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  min-height: 100vh;
  background-color: #3d3d3d;
}

.wrapper {
  -webkit-box-flex: 0;
          flex: none;
  position: relative;
}

.finish-warp {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.finish-warp.show {
  visibility: visible;
  opacity: 1;
}